<template lang="html">
  <div class="">
    <h2>{{info.title}}</h2>

    <div class="content">
      <p class="top-li">
        <span>技能列表</span>
        <span>
          <i class="fa fa-list-ul" :class="{'select': type == 'list' }" @click="handleClick('list')"></i>
          <i class="fa fa-bar-chart" :class="{'select': type == 'chart' }" @click="handleClick('chart')"></i>
        </span>
      </p>
      <ul v-show="type == 'list'" class="skills-list">
        <li v-for="item in (info && info.skills)">{{item}}</li>
      </ul>

      <div v-show="type == 'chart'" id="echart" style="background-color:rgba(0, 0, 0, 0.2); width: 500px; height: 500px;"></div>
    </div>
  </div>
</template>

<script>
import info from '@/demoData/skills.js';
// 引入 ECharts 主模块
import echarts from 'echarts';

// import echarts from 'echarts/lib/echarts';
// // 引入柱状图
// import 'echarts/lib/chart/bar';
// // 引入提示框和标题组件
// import 'echarts/lib/component/tooltip';
// import 'echarts/lib/component/title';

export default {
  name: 'Skills',
  data() {
    return {
      info: {},
      type: 'list',// list/chart
      echart: null,
    };
  },
  methods : {
    handleClick(type) {
      if(type == 'chart') {
        alert('echart还在进行中,目前实现不能符合设想。后续将调整');
      }
      this.type = type;
    },

    initEchart() {

      const option = {
        title: {
          text: '专业技能柱状图(图表这个内容还没有做完)'
        },
        tooltip: {},
        xAxis: {
          type: 'value',
          // scale: true,
          axisTick: {
            show: false
          },
          // boundaryGap: ['2%', '2%'],
          axisLabel: {
            formatter: function (value, index) {
              let res = '';
              if(index < 4) {
                res = '编程能力';
              }
              return res;
            }

          }
        },
        yAxis: {
          type: 'value',
        },
        series: [{
          type: 'bar',
          name: '测试',
          barWidth: '50%',
          data: [
            // HTML  CSS Bootstrap Less Sass Stylus JS ES6 JQuery Vue React AMD CMD NPM Yarn Gulp Webpack 调试技巧
            //学习能力
            // 编程思想 编程规范 设计模式 调试技巧
            {value: [ 0.1, 100], itemStyle:{normal:{color:'blue'}}},
            {value: [ 0.4, 90], itemStyle:{normal:{color:'blue'}}},
            {value: [ 0.6, 80], itemStyle:{normal:{color:'blue'}}},
            {value: [ 0.8, 80], itemStyle:{normal:{color:'blue'}}},


            //Bootstrap Less Sass Stylus
            {value: [ 1.2, 70], itemStyle:{normal:{color:'blue'}}},
            {value: [ 1.4, 60], itemStyle:{normal:{color:'blue'}}},
            {value: [ 1.6, 50], itemStyle:{normal:{color:'blue'}}},
            {value: [ 1.8, 45], itemStyle:{normal:{color:'blue'}}},

            //ES6 Jquery Vue Angular React
            {value: [ 2.2, 70], itemStyle:{normal:{color:'blue'}}},
            {value: [ 2.4, 80], itemStyle:{normal:{color:'blue'}}},
            {value: [ 2.6, 80], itemStyle:{normal:{color:'blue'}}},
            {value: [ 2.8, 30], itemStyle:{normal:{color:'blue'}}},
            {value: [ 3.0, 10], itemStyle:{normal:{color:'blue'}}},

            //AMD
            {value: [ 3.4, 50], itemStyle:{normal:{color:'blue'}}},

            //NPM Yarn
            //NPM
            {value: [ 3.8, 60], itemStyle:{normal:{color:'blue'}}},
            {value: [ 4.0, 50], itemStyle:{normal:{color:'blue'}}},

            //Gulp Webpack
            {value: [ 4.4, 60], itemStyle:{normal:{color:'blue'}}},
            {value: [ 4.6, 50], itemStyle:{normal:{color:'blue'}}},

            //Java Android Node
            {value: [ 5, 60], itemStyle:{normal:{color:'blue'}}},
            {value: [ 5.2, 50], itemStyle:{normal:{color:'blue'}}},
            {value: [ 5.4, 60], itemStyle:{normal:{color:'blue'}}},
          ],
        }]
      };

      this.echart.setOption(option);
    }
  },

  mounted() {
    this.info = info;
    this.echart = echarts.init(document.getElementById('echart'));
    this.initEchart();
  }
}
</script>

<style lang="stylus" scoped>
@import '../assets/variables.stylus';

h2, .content {
  box-shadow: box-shadow;
  background-color: white;
  padding: 15px;
}

.content {
  font-size: 20px;

  p {
    text-align: left;
    margin: 15px;
  }

  .top-li {
    span:nth-of-type(2) {
      margin-top: -10px;
      float: right;
      i {
        padding: 8px;
        border-radius: 50%;
        height: 22px;
        line-height: 22px;
        background-color: #4ae387;
        border: 1px solid #4ae387;

        margin: 5px;

        &.select {
          background-color: #1ec260;
          border: 1px solid #1ec260;
          cursor: not-allowed;
          box-shadow: 0 0 5px 1px #4ae387;
        }

        &:not(.select){
          box-shadow: 0 0 10px 3px #4ae387;

          &:hover {
            background-color: darken(#4ae387, 30%);
          }
        }
      }
    }
  }

  .skills-list{
    text-align: left;

    li {
      padding: 5px 0;
    }
  }

}
</style>



