<template lang="html">
  <div class="">
    <el-menu default-active="1" class="el-menu-vertical-demo" :collapse="isCollapse">
      <el-menu-item index="1" @click="handleOpenSearch">
        <i class="el-icon-search" aria-hidden="true" v-show="isCollapse"></i>
        <span slot="title" v-show="isCollapse" > 搜索页面 </span>
        <el-input  slot="title" size="small" icon="search"
          v-show="!isCollapse" v-model="keywords"
          style="width:100%;" placeholder="页面关键字"
          :on-icon-click="handleIconClick">
        </el-input>
      </el-menu-item>
    </el-menu>
    <el-menu class="el-menu-vertical-demo main-nav"
      :router="true" :default-active="activeRoute || $route.path"
      :collapse="isCollapse" @select="handleNavSelect">
      <el-menu-item :index="route.path" :route="route" v-for="route in navRouters" :key="route.name">
        <i class="fa fa-wrench" :class="route.meta.icon" aria-hidden="true"></i>
        <span slot="title" style="margin-left:0.5em">{{route.meta.des}}</span>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
/* eslint-disable no-alert, no-console */

import bus from '@/bus';
import slideRouters from '@/demoData/slidebar';
import { findRouterView } from "@/router/routerUtils";


export default {
  name: 'SlideBar',
  data() {
    return {
      isCollapse: true,
      routers: slideRouters,
      keywords: '',
      navRouters: slideRouters,
      activeRoute: '',
    };
  },
  methods: {
    handleIconClick(ev) {
      const keywords = ""+this.keywords;
      if(!keywords.trim()){
        this.tipMessage();
        this.navRouters = this.routers;
        return;
      }
      this.navRouters = [];
      const searchStr = keywords.split(' ');
      const map = new Map();
      for (var i = 0; i < searchStr.length; i++) {
        let res = findRouterView(this.routers, 'meta.keywords', searchStr[i]);
        res.forEach(item => {
          map.set(item.path, item);
        })
      }

      this.navRouters = [...map.values()];
    },

    handleOpenSearch() {
      if(this.isCollapse){
        this.keywords || this.tipMessage();
        bus.$emit('isCollapse', false);
      }
    },

    handleNavSelect(key, keyPath) {
      // this.activeRoute = key;
      this.navRouters = this.routers;
    },

    tipMessage() {
      this.$message({
        message: '请输入所要查询的页面关键字。多个以空格隔开。eg:经历 个人',
        showClose: true,
        duration: 2000,
      });
    }
  },
  mounted() {
    bus.$on('isCollapse', (isCollapse) => {
      this.isCollapse = isCollapse;
    });
  },
};
</script>

<style lang="less" scoped>
.main-nav{
  .is-active {
    i {
      transform: scale(1.5);
    }
  }
}
</style>



