import { Button,
  Row, Col,
  Menu, Submenu, MenuItem, MenuItemGroup,
  Radio, RadioGroup, RadioButton,
  Tooltip, Message,
  Card,
  Input } from 'element-ui';

const eleCmpt = [
  Button,
  Row, Col,
  Menu, Submenu, MenuItem, MenuItemGroup,
  Radio, RadioGroup, RadioButton,
  Tooltip,
  Card,
  Input];

const eleServer = new Map();
eleServer.set('$message' , Message);

export {
  eleCmpt,
  eleServer
};



