import LayoutPage from '@/views/layout/Layout';
import RouterViewPage from '@/views/layout/RouterView';
import ErrorPage from '@/views/error/Error';
import navLayerPage from '@/views/navLayer/navLayer';

import IntroView from '@/components/Intro';
import SkillsView from '@/components/Skills';
import WorkExperienceView from '@/components/WorkExperience';
import ProjectExperienceView from '@/components/ProjectExperience';
import CollegeView from '@/components/College';
import SelfEvaluationView from '@/components/SelfEvaluation';
import CareerPlanView from '@/components/CareerPlan';
import TestView from '@/components/TestView';
import TestNav from '@/components/TestNav';

const routers = [
  {
    path: '',
    component: LayoutPage,
    children: [
      {
        path: '/intro',
        alias: '',
        name: 'Intro',
        component: IntroView,
        meta:{
          des: '个人简介',
          icon: 'fa-address-card-o',
          keywords: '个人, 简历, 个人简历',
        }
      },
      {
        path: '/skills',
        name: 'Skills',
        component: SkillsView,
        meta:{
          des: '专业技能',
          icon: 'fa-wrench',
          keywords: '专业, 技能, 专业技能',
        },
      },
      {
        path: '/work-experience',
        name: 'WorkExperience',
        component: WorkExperienceView,
        meta:{
          des: '职业经历',
          icon: 'fa-group',
          keywords: '职业, 经历, 职业经历',
        }
      },
      {
        path: '/project-experience',
        name: 'ProjectExperience',
        component: ProjectExperienceView,
        meta:{
          des: '项目经验',
          icon: 'fa-file-code-o',
          keywords: '项目, 经验, 项目经验',
        }
      },
      {
        path: '/college',
        name: 'College',
        component: CollegeView,
        meta:{
          des: '教育经历',
          icon: 'fa-graduation-cap',
          keywords: '教育, 经历, 教育经历',
        }
      },
      {
        path: '/self-evaluation',
        name: 'SelfEvaluation',
        component: SelfEvaluationView,
        meta:{
          des: '个人评价',
          icon: 'fa-paint-brush',
          keywords: '个人, 评价, 个人评价',
        }
      },
      {
        path: '/career-plan',
        name: 'CareerPlan',
        component: CareerPlanView,
        meta:{
          des: '职业理想',
          icon: 'fa-paper-plane-o',
          keywords: '职业, 理想, 职业理想',
        }
      },
      {
        path: '/test',
        component: RouterViewPage,
        children: [
          {
            path: 'test2',
            name: 'Test',
            component: TestView,
            meta:{
              des: '测试二级路由',
              icon: 'fa-object-group',
              keywords: '测试 二级路由',
            },
          },
        ]
      },
      {
        path: '/navPage',
        name: 'navPage',
        component: navLayerPage,
        meta:{
          des: '导航页',
          icon: '',
          keywords: '导航',
        }
      },
      {
        path: '/navtest',
        name: 'navTest',
        component: TestNav,
        meta:{
          des: '导航测试页',
          icon: '',
          keywords: '导航 测试',
        }
      },
    ],
  },
  {
    path: '404',
    alias: '/error',
    name: 'Error',
    component: ErrorPage,
  }
]

export default routers;



