<template lang="html">
  <div class="">
    <h2>这个页面是nav页</h2>
    <div class="page-info" v-for="(pages, typeindex) in pageInfo" :key="typeindex">
      <div class="type">
        <span v-for="char in (pages.type + typeindex)">{{char}}</span>
      </div>
      <el-row class="pages" :gutter="20">
        <el-col :span="6" v-for="(page, index) in pages.pages" :key="page.name+index">
          <div class="" @click="handleClickCard(pages.type + typeindex + page.name + index)">
            <el-card :body-style="{ padding: '0px' }" >
              <img :src="page.thumb || require('../../assets/logo.png')" class="image">
              <p>{{pages.type + typeindex + page.name + index}}</p>
            </el-card>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
//TODO 此页面需要进一步修改 数据 + 样式
export default {
  data() {
    return {
      pageInfo: []
    }
  },
  methods: {
    handleClickCard(val) {
      this.$router.replace({ path: '/navtest', query: { name: val }});
    }
  },

  mounted() {
    this.pageInfo = [
                      {type: '类别', pages: [{name: '页面', thumb: ''},{name: '页面', thumb: ''}]},
                      {type: '类别', pages: [{name: '页面', thumb: ''},{name: '页面', thumb: ''}]},
                      {type: '类别', pages: [{name: '页面', thumb: ''},{name: '页面', thumb: ''}]},
                    ]
  }
}
</script>

<style lang="less">
  .page-info {
    margin: 15px;
    display: flex;
    flex-direction: row;

    .type {
      flex: 0 0 auto;
      display: flex;
      padding: 10px;
      flex-direction: column;
      justify-content: center;
      background-color: #13CE66;
      font-size: 18px;
      font-weight: bold;
    }

    .pages {
      flex: 1 1 auto;

      img {
        width: 100%;
        height: auto;
        border-bottom: 1px solid #13CE66
      }
    }
  }

</style>



