/**
 * 通过key-value查找数组中对应值的对象
 * @param  {Array} arr        包含待查找对象的数组
 * @param  {String} key       要查找的key
 * @param  {String} value     要查找的value
 * @param  {String} separator 如果值存在分隔符，使用分隔符
 * @return {Array}            符合对象的数组
 */
function findArrByKV(arr, key, value, separator){
  let resArr = [];
  for (var i = 0, l = arr.length; i < l; i++) {
    const item = arr[i];

    let val = item;
    let path = (key || "").split(".");
    for (var j = 0, k; k = path[j++];) {
        val = val[k];
        if (!val) break;
    }

    const regexp = new RegExp(value);
    if(regexp.exec(val)){
      resArr.push(item);
    }

    // console.log('val',val);
    // if(val){
    //   if(separator){
    //     val = val.split(separator);
    //     if(separator !== ' '){
    //       val = val.map(function(item){
    //         return item.trim();
    //       })
    //     }
    //   }
    //
    //   if((val == value || (val.indexOf && val.indexOf(value) != -1))){
    //     resArr.push(item);
    //   }
    // }

  }
  return resArr;
}

export {
  findArrByKV,
}



