const routers = [
  {
    path: '/intro',
    alias: '',
    name: 'Intro',
    meta:{
      des: '个人简介',
      icon: 'fa-address-card-o',
      keywords: '个人, 简历, 个人简历',
    }
  },
  {
    path: '/skills',
    name: 'Skills',
    meta:{
      des: '专业技能',
      icon: 'fa-wrench',
      keywords: '专业, 技能, 专业技能',
    },
  },
  {
    path: '/work-experience',
    name: 'WorkExperience',
    meta:{
      des: '职业经历',
      icon: 'fa-group',
      keywords: '职业, 经历, 职业经历',
    }
  },
  {
    path: '/project-experience',
    name: 'ProjectExperience',
    meta:{
      des: '项目经验',
      icon: 'fa-file-code-o',
      keywords: '项目, 经验, 项目经验',
    }
  },
  {
    path: '/college',
    name: 'College',
    meta:{
      des: '教育经历',
      icon: 'fa-graduation-cap',
      keywords: '教育, 经历, 教育经历',
    }
  },
  {
    path: '/self-evaluation',
    name: 'SelfEvaluation',
    meta:{
      des: '个人评价',
      icon: 'fa-paint-brush',
      keywords: '个人, 评价, 个人评价',
    }
  },
  {
    path: '/career-plan',
    name: 'CareerPlan',
    meta:{
      des: '职业理想',
      icon: 'fa-paper-plane-o',
      keywords: '职业, 理想, 职业理想',
    }
  },
  {
    path: '/test',
    children: [
      {
        path: 'test2',
        name: 'Test',
        meta:{
          des: '测试二级路由',
          icon: 'fa-object-group',
          keywords: '测试 二级路由',
        },
      },
    ]
  },
]

export default routers;



