import Vue from 'vue'
import Vuex from 'vuex'

import * as types from './mutation-types'

Vue.use(Vuex)

const state = {
  isOpenRight: false,
  pageInfo: {
    // userInfo: {},
    // authInfo: [],
  },
}

const mutations = {
  [types.toggleRightBar] (state, isOpenRight) {
    state.isOpenRight = isOpenRight;
  },

  [types.setPageInfo] (state, pageInfo) {
    state.pageInfo = pageInfo;
  },
}

const getters = {
  userInfo: state => state.pageInfo.userInfo,
  authInfo: state => state.pageInfo.authInfo,
}

// 写这个纯粹是为了占位
const actions = {
  toggleRightBar: ({ commit }, isOpenRight ) => commit(types.toggleRightBar, isOpenRight),
  setPageInfo: ({ commit }, PageInfo ) => commit(types.setPageInfo, PageInfo),
}

export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})



