<template lang="html">
  <div class="error">
    <div class="">
      <p>404</p>
      <p>o(╥﹏╥)o</p>
    </div>

    <ul>
      <li v-for="(item, index) in error">
        {{item ? item : '我犯错了'}}
      </li>
    </ul>
  </div>
</template>

<script>
import errorArr from '@/demoData/error.js';

export default {
  data() {
    return {
      error : ['我犯错了', '打一顿就好了'],
    }
  },
  methods: {
    randomError() {
      const len = errorArr.length;
      let index = parseInt(Math.random()*len);
      this.error = errorArr[index];
    }
  },
  mounted() {
    this.randomError();
  }
}
</script>

<style lang="stylus">
  $errQ = lightness(#20A0FF, 70%);
  $errInfo = lightness(#FF4949, 70%);
  $errA = #13CE66;
  .error{
    p {
      font-size: 50px;
      letter-spacing: 0.3em;
      color: $errInfo;

      &:nth-of-type(2){
        margin-top: -0.5em;
      }
    }

    ul {
      list-style: none;
      padding-left: 0;

      li{
        font-size: 26px;
        &:nth-of-type(1){
          color: $errQ;
        }
        &:nth-of-type(2){
          margin-top: 10px;
          color: $errA;
        }
      }
    }
  }
</style>



