<template lang="html">
  <div class="" @click="handleMain">
    <div class="slide-bar">
      <slide-bar></slide-bar>
    </div>
    <transition
      name="fade-up" mode="out-in"
      appear appear-class="fade-up-leave-to" appear-active-class="fade-up-enter-active">
      <router-view class="main-view"></router-view>
    </transition>
  </div>
</template>

<script>
import SlideBar from '@/views/layout/SlideBar';

export default {
  name: 'AppMain',
  components: {
    SlideBar,
  },
  methods: {
    handleMain(event) {
      if(this.$store.state.isOpenRight){
        this.$store.dispatch('toggleRightBar', false);
      }
    }
  }
};
</script>

<style lang="less" scoped>
@import '../../assets/variables.less';
.slide-bar{
  background-color: rgba(255,255,255,0.9);
  .mixin-stroke();
}

.main-view {
  overflow-y: scroll;
  margin: 15px;
}

</style>

<style media="screen" lang="less">
.fade-up-enter-active {
  transition: all .4s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.fade-up-leave-active {
  transition: all .4s ease;
}

.fade-up-enter {
  transform: translateY(20px);
  opacity: 0;
}

.fade-up-leave-to {
  transform: translateY(-20px);
  opacity: 0;
}
</style>



