<template>
  <div>
    <h2>{{info.title}}</h2>
    <ul>
      <li v-for="item in (info && info.person)">
        <span>{{item.name + ': '}}</span>
        <span v-if="!item.link">{{item.value}}</span>
        <a v-else :href="item.value">{{item.value}}</a>
      </li>
    </ul>
    <ul>
      <li v-for="item in (info && info.career)">
        <span>{{item.name + ': '}}</span>
        <span>{{item.value}}</span>
      </li>
    </ul>
  </div>
</template>

<script>
import info from '@/demoData/intro.js';

export default {
  name: 'Intro',
  data() {
    return {
      info: {},
    };
  },

  mounted() {
    this.info = info;
  }
};
</script>

<style lang="stylus" scoped>
@import '../assets/variables.stylus';

h2, ul {
  box-shadow: box-shadow;
  background-color: white;
  padding: 15px;
}

ul {
  font-size: 20px;
  list-style: none;
  li {
    text-align: left;
    margin: 15px;

    span:nth-of-type(1) {
      display: inline-block;
      text-align: justify;
      text-align-last:justify;
      width: 5em;
      margin-right: 20px;
      color: #666;;
    }
  }
}
</style>



