<template lang="html">
  <div class="container">
    <header-bar class="header-nav"></header-bar>

    <app-main class="app-main"></app-main>

    <right-bar></right-bar>
  </div>

</template>

<script>
import HeaderBar from '@/views/layout/HeaderBar';
import AppMain from '@/views/layout/AppMain';
import RightBar from '@/views/layout/RightBar'

export default {
  name: 'Layout',
  data() {
    return {
      // pageInfo: {},
    }
  },
  components: {
    HeaderBar,
    AppMain,
    RightBar,
  },
  methods: {

  },

  mounted() {
    const pageInfo = {
      userInfo: { title: '邹鑫简历', name: '邹鑫', avator: '', },
      authInfo: {},
    }
    this.$store.dispatch('setPageInfo', pageInfo);
  }
};
</script>

<style lang="less">
@import '../../assets/variables.less';
.container{
  position: absolute;
  height: 100%;
  width: 100%;
  min-width: 475px;
  background-color: #f0f0f0;
}

.header-nav {
  background-color: #fff;
  background-color: rgba(255,255,255,0.9);
  height: 50px;
  line-height: 50px;
  color: white;
  position: fixed;
  width: 100%;
  z-index:5;
  .mixin-stroke();
}

.header-nav, .app-main {
  display: flex;
  justify-content: space-between;
  flex-direction: row;

  > div:nth-of-type(1){
    flex: 0 0 auto;
  }

  > div:nth-of-type(2){
    flex: 1 1 auto;
  }
}

.header-nav{
  color: @header-color;
  font-weight: @header-blod;
}

.app-main{
  padding-top: 50px;
  height: 100%;
  box-sizing: border-box;
}


</style>



