<template lang="html">
  <div>
    <div class="info-web">
      <el-menu default-active="1" class="el-menu-vertical-demo" :collapse="isCollapse">
        <el-menu-item index="1">
          <i class="fa fa-newspaper-o fa-lg" aria-hidden="true"
            style="line-height: 50px; height: 50px; vertical-align: top;"></i>
          <span slot="title" style="overflow:hidden; text-overflow:ellipsis;"> {{userInfo && userInfo.title}} </span>
        </el-menu-item>
      </el-menu>
    </div>
    <div class="info-header">
      <div class="info-menu">
        <el-button class="menu-btn" type="info" @click="toggleSlideBar">
          <i class="fa fa-bars fa-lg" aria-hidden="true"></i>
        </el-button>
        <transition
        appear appear-active-class="appear-bounce-active">
          <span class="sub-nav" @click="handleSubNav">
            <i class="fa fa-th-large fa-lg" aria-hidden="true"></i>
          </span>
        </transition>
      </div>
      <div class="info-title">
        <transition
          name="fade-up" mode="out-in"
          appear appear-class="fade-up-leave-to" appear-active-class="fade-up-enter-active">
          <div class="page-title" :key="$route.name"> {{$route.meta.des || '简历'}} </div>
        </transition>
      </div>
      <div class="info-person">
        <div @click="popRightBar">
          <img :src="userInfo && userInfo.avatar || require('../../assets/logo.png')  " alt="">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable no-alert, no-console */

import bus from '@/bus';
import { mapGetters } from 'vuex'

export default {
  name: 'HeaderBar',
  data() {
    return {
      isCollapse: true,
    };
  },
  computed: mapGetters([
    'userInfo',
  ]),
  methods: {
    toggleSlideBar() {
      this.isCollapse = !this.isCollapse;
      bus.$emit('isCollapse', this.isCollapse);
    },

    handleSubNav() {
      this.$router.push({ path: '/navPage' })
    },

    popRightBar() {
      let isOpenRight = this.$store.state.isOpenRight;
      isOpenRight = !isOpenRight;
      this.$store.dispatch('toggleRightBar', isOpenRight);
    },
  },
  mounted() {
    bus.$on('isCollapse', (isCollapse) => {
      this.isCollapse = isCollapse;
    });
  },
};
</script>

<style lang="less" scoped>
@import '../../assets/variables.less';

.info-web{
  .el-menu-item{
    height: 50px;
    line-height: 50px;
    color: @header-color;
    font-weight: @header-blod;

    &:hover{
      background-color: white;
    }
  }
}

.info-header{
  flex: 1 1 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-left: 1px solid rgba(255,255,255,0.9);
}

.info-menu{
  text-align: left;

  .menu-btn {
    background: transparent;
    border: 0;

    i {
      color: @header-color;
      font-weight: @header-blod;
    }

    &:hover i{
      transition: 0.3s;
      transform: scale3d(1.1, 1.1, 1);
    }
  }

  .sub-nav{
    display: inline-block;
    box-sizing: border-box;
    text-align: center;
    background-color: #fff;
    margin-left: 10px;
    width: 44px;
    border: 1px solid #fff;
    border-radius: 50%;
    height: 44px;
    line-height: 50px;
    vertical-align: middle;
    box-shadow: 0 0 2px 4px rgba(224, 224, 224, 0.7);
    &:hover {
      background-color: rgba(245, 245, 245, 0.2);
      border: 1px solid rgba(224, 224, 224, 0.1);
      box-shadow: 0 0 2px 4px rgb(224, 224, 224);
    }

    i {
      vertical-align: baseline;
    }

  }
}

.info-title {
  flex: 1 1 auto;
  margin-left: 15px;

  .page-title {
    padding-left: 15px;
    text-align: left;
  }
}

.info-person{
  margin-right: 15px;

  img{
    width:40px;
    height:40px;
    border-radius:50%;
    vertical-align: middle;
    border:1px solid @header-color;
  }
}

.appear-bounce-active {
  animation: bounce-in 1s;
}


@keyframes bounce-in {
  0% {
    transform: scale(0.9);
  }

  25% {
    transform: scale(1.1);
  }

  50% {
    transform: scale(0.9);
  }

  75% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

</style>



