import { findArrByKV } from '../utils/data';

function findRouterView(routers, key, value, separator) {
  // console.log(1);
  // console.log('findArrByKV',findArrByKV);
  //1. 所有路由均生成为一级路由
  let resRouters = jointRouter(routers);

  //2. 查找1级路由组中符合的情况
  resRouters = findArrByKV(resRouters, key, value, separator);

  return resRouters;
}

/**
 * 生成一级路由
 * @param  {Array} routers        路由对象
 * @param  {String} prefixParentPath    递归时用到的路由前缀路径即当前路由的父路径
 * @param  {Array} childRouters   递归时用到的已处理的路由对象数组
 * @return {Array}                拼接完成路径后的路由对象数组
 */
function jointRouter(routers, prefixParentPath, childRouters){
  var resRouters = childRouters || [];
  for (var i = 0; i < routers.length; i++) {
    var router = routers[i];
    let prefixPath = prefixParentPath ? prefixParentPath + router.path : router.path;
    var children = router.children;
    if(Array.isArray(children)){
      jointRouter(children, prefixPath +'/', resRouters);
    }else{
      router.path = prefixPath;
      resRouters.push(router);
    }
  }
  return resRouters;
}

export {
  findRouterView,
}



